@-webkit-keyframes to-right {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  to {
    -webkit-transform: translateX(100vw);
            transform: translateX(100vw);
  }
}

@keyframes to-right {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  to {
    -webkit-transform: translateX(100vw);
            transform: translateX(100vw);
  }
}

@-webkit-keyframes out-pick {
  from, 20%, to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
}

@keyframes out-pick {
  from, 20%, to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
}

@-webkit-keyframes out-cut {
  from, 50%, to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }

  75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
}

@keyframes out-cut {
  from, 50%, to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }

  75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
}

@-webkit-keyframes out-jump {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  to {
    -webkit-transform: translate(100%);
            transform: translate(100%);
  }
}

@keyframes out-jump {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  to {
    -webkit-transform: translate(100%);
            transform: translate(100%);
  }
}

.animate-pick {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.animate-cut {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.animate-jump {
  position: absolute;
  left: 0;
  top: 20%;
  -webkit-animation: to-right 1s linear;
          animation: to-right 1s linear;
}

.game-area {
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  -o-transition: transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}

.game-area.skill-pick {
  -webkit-animation: out-pick 0.3s 0.2s;
          animation: out-pick 0.3s 0.2s;
}

.game-area.skill-cut {
  -webkit-animation: out-cut 0.3s 0.2s;
          animation: out-cut 0.3s 0.2s;
}

.game-area.skill-jump {
  -webkit-animation: out-jump 1s linear;
          animation: out-jump 1s linear;
}


