.home__header {
  height: 5.6rem;
  background: #01bbba;
  position: relative;
  -webkit-box-shadow: 0 0.02666667rem 0.13333333rem rgba(0,0,0,0.5);
          box-shadow: 0 0.02666667rem 0.13333333rem rgba(0,0,0,0.5);
}

@-webkit-keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.home__logo {
  -webkit-animation: show 4s;
          animation: show 4s;
}

.home__logo:after {
  content: '';
  display: block;
  background: url('./logo.svg') 100% / 100%;
  width: 6.4rem;
  height: 5.12rem;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.home__body {
  margin-top: 2.66666667rem;
}

.home__button {
  font-size: 0.45333333rem;
  display: block;
  background-color: #01bbba;
  color: #fff;
  border-radius: 0.66666667rem;
  height: 1.33333333rem;
  width: 5.6rem;
  margin: 0.53333333rem auto;
}


