@-webkit-keyframes loading {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

@keyframes loading {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

.loading {
  position: absolute;
  background: #01bbba;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-animation: loading 500ms 2500ms;
          animation: loading 500ms 2500ms;
  overflow: hidden;
}

.loading__left,
.loading__right {
  position: absolute;
  font-family: "iconfont" !important;
  font-size: 1.6rem;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  color: #fff;
  top: 50%;
  left: 50%;
  width: 1.6rem;
  height: 1.6rem;
}

.loading__left:before,
.loading__left:after,
.loading__right:before,
.loading__right:after {
  position: absolute;
}

@-webkit-keyframes left {
  from, to {
    left: 0;
  }

  30%, 70% {
    left: 50%;
  }
}

@keyframes left {
  from, to {
    left: 0;
  }

  30%, 70% {
    left: 50%;
  }
}

@-webkit-keyframes right {
  from, to {
    left: 100%;
  }

  30%, 70% {
    left: 50%;
  }
}

@keyframes right {
  from, to {
    left: 100%;
  }

  30%, 70% {
    left: 50%;
  }
}

@-webkit-keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes hide {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes hide {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.loading__left {
  -webkit-transform: translate(-100%,-50%);
      -ms-transform: translate(-100%,-50%);
          transform: translate(-100%,-50%);
  -webkit-animation: left 2500ms forwards;
          animation: left 2500ms forwards;
}

.loading__left:before {
  content: '\e62b';
  -webkit-animation: hide 1ms 1000ms forwards;
          animation: hide 1ms 1000ms forwards;
}

.loading__left:after {
  opacity: 0;
  content: '\e62e';
  -webkit-animation: show 1ms 1000ms forwards;
          animation: show 1ms 1000ms forwards;
}

.loading__right {
  -webkit-transform: translate(0%,-50%);
      -ms-transform: translate(0%,-50%);
          transform: translate(0%,-50%);
  -webkit-animation: right 2500ms forwards;
          animation: right 2500ms forwards;
}

.loading__right:before {
  content: '\e62c';
  -webkit-animation: hide 1ms 1000ms forwards;
          animation: hide 1ms 1000ms forwards;
}

.loading__right:after {
  opacity: 0;
  content: '\e62d';
  -webkit-animation: show 1ms 1000ms forwards;
          animation: show 1ms 1000ms forwards;
}


