.button {
  -webkit-transition: -webkit-transform 200ms,-webkit-box-shadow 200ms;
  transition: -webkit-transform 200ms,-webkit-box-shadow 200ms;
  -o-transition: transform 200ms,box-shadow 200ms;
  transition: transform 200ms,box-shadow 200ms;
  transition: transform 200ms,box-shadow 200ms,-webkit-transform 200ms,-webkit-box-shadow 200ms;
  -webkit-box-shadow: 0.02666667rem 0.13333333rem 0.13333333rem rgba(0,0,0,0.5);
          box-shadow: 0.02666667rem 0.13333333rem 0.13333333rem rgba(0,0,0,0.5);
  outline: none;
  border: none;
}

.button.is-press {
  -webkit-transform: scale(0.98) translateY(-0.02666667rem);
      -ms-transform: scale(0.98) translateY(-0.02666667rem);
          transform: scale(0.98) translateY(-0.02666667rem);
  -webkit-box-shadow: 0.02666667rem 0.02666667rem 0.02666667rem rgba(0,0,0,0.5);
          box-shadow: 0.02666667rem 0.02666667rem 0.02666667rem rgba(0,0,0,0.5);
}


